@import '../../styles/MIXINS/color';

.loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color--loader_bgc-color;
    z-index: 10;

    &__spiner {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        display: block;
        margin: 0 auto;
        position: relative;
        left: -100px;
        box-sizing: border-box;
        animation: shadowRolling 2s linear infinite;
    }
}

@keyframes shadowRolling {
    0% {
        box-shadow: 0px 0 $color--loader_animate, 0px 0 $color--loader_animate, 0px 0 $color--loader_animate, 0px 0 $color--loader_animate;
    }

    12% {
        box-shadow: 100px 0 $color--loader_sfera, 0px 0 $color--loader_animate, 0px 0 $color--loader_animate, 0px 0 $color--loader_animate;
    }

    25% {
        box-shadow: 110px 0 $color--loader_sfera, 100px 0 $color--loader_sfera, 0px 0 $color--loader_animate, 0px 0 $color--loader_animate;
    }

    36% {
        box-shadow: 120px 0 $color--loader_sfera, 110px 0 $color--loader_sfera, 100px 0 $color--loader_sfera, 0px 0 $color--loader_animate;
    }

    50% {
        box-shadow: 130px 0 $color--loader_sfera, 120px 0 $color--loader_sfera, 110px 0 $color--loader_sfera, 100px 0 $color--loader_sfera;
    }

    62% {
        box-shadow: 200px 0 $color--loader_animate, 130px 0 $color--loader_sfera, 120px 0 $color--loader_sfera, 110px 0 $color--loader_sfera;
    }

    75% {
        box-shadow: 200px 0 $color--loader_animate, 200px 0 $color--loader_animate, 130px 0 $color--loader_sfera, 120px 0 $color--loader_sfera;
    }

    87% {
        box-shadow: 200px 0 $color--loader_animate, 200px 0 $color--loader_animate, 200px 0 $color--loader_animate, 130px 0 $color--loader_sfera;
    }

    100% {
        box-shadow: 200px 0 $color--loader_animate, 200px 0 $color--loader_animate, 200px 0 $color--loader_animate, 200px 0 $color--loader_animate;
    }
}