@import '../../styles/MIXINS/color';
@import '../../styles/MIXINS/mixins';

.product {
    padding: 30px;
    border-radius: 24px;
    background-color: $color--bgc_product;

    &:hover {
        box-shadow: 24px 24px 40px rgba(75, 207, 160, 0.08);
    }

    &__category {
        font-weight: 400;
        text-transform: uppercase;
        opacity: .5;
        margin-bottom: 8px;
        @include font-size(12px, 16px, 320px, 1500px);
    }

    &__title {
        @include font-size(20px, 40px, 320px, 1500px);
        font-weight: 400;
    }

    &__main {
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__price {
        display: flex;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }

    &__currency {
        @include font-size(25px, 30px, 320px, 1500px);
        @include margin-top(0px, 12px, 320px, 1500px);
        font-weight: 400;
        margin-right: 5px;
    }

    &__sum {
        @include font-size(35px, 60px, 320px, 1500px);
        font-weight: 400;
    }

    &__button {
        @include font-size(13px, 16px, 320px, 1500px);
        font-weight: 500;
        text-transform: uppercase;
    }
}