@import '../../styles/MIXINS/color';

.input {
    padding: 16px;
    border-radius: 16px;
    border: 1px solid $color--input_border;
    outline: none;
    font-size: 16px;
    font-weight: 400;
    color: $color--input-color;

    &__success {
        border: 1px solid $color--input_border-focus;
    }

    &__error-border {
        border: 1px solid $color--input_border-error
    }

    &__error {
        font-size: 12px;
        font-weight: 400;
        margin-top: 2px;
        color: $color--input_error;
    }

}