@import '../../styles/MIXINS/color';
@import '../../styles/MIXINS/mixins';

.modal {
    position: fixed;
    top: 0;
    left: 0;
    background-color: $color--modal_overlay;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    z-index: -1;

    &__active {
        opacity: 1;
        z-index: 10;
    }

    &__content {
        position: relative;
        background-color: $color--modal_bgc-container;
        border-radius: 24px;
        padding: 50px 45px;
        width: 380px;
        @include margin-left(15px, 0px, 400px, 1500px);
        @include margin-right(15px, 0px, 400px, 1500px);
    }

    &__close {
        position: absolute;
        right: -12px;
        top: -12px;
        width: 40px;
        height: 40px;
        border-radius: 100%;
        border: none;
        font-size: 14px;
        background-color: $color--modal_close;
        cursor: pointer;

        &:hover,
        active {
            background-color: $color--modal_close-hover;
            color: $color--modal_close-color
        }
    }

    &__information {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;
    }

    &__sup-title {
        font-weight: 400;
        color: $color--modal_sup-title;
        opacity: .5;
        text-transform: uppercase;
        @include font-size(14px, 16px, 400px, 1500px);
    }

    &__title {
        font-weight: 400;
        color: $color--modal_title;
        margin-top: 8px;
        margin-bottom: 15px;
        @include font-size(25px, 40px, 400px, 1500px);
    }

    &__price {
        display: flex;
    }

    &__currency {
        font-weight: 400;
        margin-right: 5px;
        color: $color--modal_currency;
        @include font-size(25px, 30px, 400px, 1500px);
        @include margin-top(0px, 10px, 400px, 1500px);
    }

    &__sum {
        font-weight: 400;
        color: $color--modal_sum;
        @include font-size(35px, 60px, 400px, 1500px);
    }

    &__input {

        &:not(:first-child) {
            margin-top: 16px;
        }
    }

    &__button {
        @include margin-top(15px, 30px, 400px, 1500px);
    }
}