@import '../../styles/MIXINS/mixins';

.main {
    max-width: 1150px;
    margin: 0 auto;
    padding: 100px 10px;

    &__button-box {
        @include margin-top(35px, 70px, 320px, 1500px);
        width: 100%;
        display: flex;
        justify-content: center;
    }

    &__button {
        @include font-size(15px, 24px, 320px, 1500px);
        font-weight: 400;
        padding-left: 48px;
        padding-right: 48px;
    }
}