@import '../../styles/MIXINS/color';

.button {
    background-color: $color--button_success;
    border: none;
    color: #fff;
    padding: 16px;
    border-radius: 16px;
    font-size: 14px;
    cursor: pointer;

    &:hover,
    &:active {
        background-color: $color--button_success-hover;
    }

    &:not([hover]):disabled {
        cursor: no-drop;
        background-color: $color--button_disabled;
    }

}

.success {
    background-color: $color--button_success;
    color: $color--button_success_color;

    &:hover,
    &:active {
        background-color: $color--button_success-hover;
    }
}

.outline {
    background-color: $color--button_outline;
    color: $color--button_outline_color;
    border: 1px solid #0000001a;

    &:hover,
    &:active {
        background-color: $color--button_outline-hover;
        color: $color--button_outline-hover-color;
    }
}